export default {
	namespaced: true,

	state: () => ({
		//
		//6
		//{goods_id,goods_name,goods_price,goods_count,goods_logo,goods_state}

		cart: []
	}),
	mutations: {
		//
		addToCart(state, goods) {
			const findResult = state.cart.find(x => x.goods_id === goods.goods_id)

			if (!findResult) {
				state.cart.push(goods)
			} else {
				// findResult.goods_count++;
			}
		}
	},
	getters: {}
}